<template>
  <div id="app">
      <b-navbar toggleable="md" type="dark" class="nav-background">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand href="/">Mr. Stock</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <b-navbar-nav class="ml-auto">
          <!---->
          <b-navbar-nav>
            <b-nav-item href="/"></b-nav-item>
            <b-nav-item v-if="!loggedIn" href="/login"></b-nav-item>
            <b-nav-item v-if="!loggedIn" href="/signup"></b-nav-item>
            <b-nav-item v-if="loggedIn" href="/logout"></b-nav-item>
          </b-navbar-nav>

          <!---->
          <b-nav-item-dropdown right>
            <template slot="button-content">
              <em></em>
            </template>
            <b-dropdown-item href="/main/profile"></b-dropdown-item>
            <b-dropdown-item href="/main/userlist"> </b-dropdown-item>
            <b-dropdown-item href="/main/settings"></b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
    <router-view/>

    <section class= "footer">
      <div class = "inner">
       : , , &nbsp;&nbsp;: 010-5040-9375<br>
       :     206  &nbsp;&nbsp;
       <br>Copyright  2018 Ajou media Mr.Stock. All Rights Reserved.
      </div>
    </section>
  </div>
</template>

<script>
export default {
  computed: {
    loggedIn() {
      return this.$store.getters.loggedIn
    }
  }
};
</script>

<style>
body {
  background-color: lightgray;
  min-height: 100%;
  margin: 0;
}
.nav-background {
  background-color: black;
}
.footer{
  position: relative;
  width: 100%;
  left:0px;
  bottom:0px auto 0px;
  margin-top: 100px;
  border-top:1px solid #666;
  background-color: #333;

}
.inner{
  max-width: 1400px;
  padding: 0;
  margin: 0 auto;
  padding-top: 14px;
  padding-bottom: 26px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;

}
.indigo{
  color: indigo;
}
</style>



